.ourClientDetails {
  margin-top: 50px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.clientTitle {
  margin-top: 20px;
  font-size: 20px;
}

.clientSubTitle {
  margin-top: 20px;
  font-size: 40px;
}

.clientFeedbackContainer {
  margin-top: 20px;
  display: flex;
}

.aboutClinetBox {
  width: 350px;
  border: 1px solid var(--cardBorderColor);
  background-color: white;
  margin: 10px;
  padding: 10px;
  border-radius: 20px;
}

.clientImg {
  height: 70px;
  width: 70px;
  background-color: green;
  border-radius: 10px;
}

.clientName {
  margin-top: 20px;
  color: var(--darkText);
}

.clientStatus {
  margin-top: 10px;
  color: var(--themeColor);
}

.clientDetails {
  font-size: 14px;
  margin-top: 10px;
  color: var(--lightText);
}