html {
  /*Core color codes------global color codes #224C98 */
  --themeColor: #207ef5;
  /* #224C98;/*#263c63;/* #1b3d7a;#4285F4*/
  --darkText: #1b1e24;
  /*#29303d;/*#132545;/*#0e1c36;*/
  --lightText: #5e5d5d;
  --cardBorderColor: #d3d3d3;
  --themeColorText: #207ef5;
  --cardBorderColorLight: #e8e8e8;

  --bgColor: #f2f2f2;
}

.website {
  width: 100vw;
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid red;
}

.innerDiv {
  width: 1200px;
  background-color: white;
}


@media screen and (max-width: 1200px) {

.innerDiv{
width: 90%;

}	


}




.topbar {
  width: 100%;
  border: 1px solid red;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.rightContainer {
  display: flex;
}

.firstTopContainer {
  display: flex;
}

.emailIcon {
  margin-left: 10px;
  height: 20px;
  width: 20px;
  background-color: #5e5d5d;
}

.textEmail {
  margin-left: 10px;
}

.secTopContainer {
  margin-left: 20px;
  border: 1px solid green;
  display: flex;
}

.mobileIcon {
  margin-left: 10px;
  height: 20px;
  width: 20px;
  background-color: #5e5d5d;
}

.mobileNumber {
  margin-left: 10px;
}

.socialiconContainer {
  background-color: #207ef5;
  border: 1px solid red;
  display: flex;
  margin-right: 10px;
}

.fbicon {
  margin: 10px;
  background-color: grey;
  height: 26px;
  width: 26px;
}

.topNavigationBar {
  padding-top: 10px;
  padding-bottom: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 1px solid red;
}

.nFirstCotainer {
  display: flex;
  align-items: center;
}
.logo {
  height: 50px;
  width: 100px;
  background-color: #207ef5;
}

.Name {
  margin-left: 10px;
  font-size: 26px;
  font-weight: bold;
}
.navigationMenu {
  display: flex;
}






.websiteContent2 {
  margin-top: 50px;
  width: 100%;
  display: flex;
}

.HomeImageContainer2 {
  width: 50%;
  background-color: green;
}

.detailContainer2 {
  margin-left: 20px;
  width: 50%;
}

.aboutHeading1 {
  color: var(--themeColor);
  font-size: 20px;
}

.aboutHeading2 {
  margin-top: 20px;
  color: var(--darkText);
  font-size: 40px;
}

.aboutDetails {
  margin-top: 20px;
  width: 280px;
  font-size: 16px;
  color: var(--lightText);
}




















.footer{
    margin-top: 40px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #207ef5;
    padding: 10px;
}

.block1{
    width: 30%;

    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: start;
}

.logotextContainer{
    display: flex;
    justify-content: center;
    align-items: center;
}

.logopic{
    width: 100px;
    background-color: white;
    height: 50px;
}

.websiteNametext{
    margin-left: 10px;
    font-size: 20px;
}

.websiteDetail{
    margin-top: 10px;
    color: white;
    font-size: 14px;
}


.block2{
    width: 20%;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: start;
}

.cont1{
    margin-top: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.cont1Icon{
    height: 30px;
    width: 30px;
    background-color: white;
    border-radius: 50%;
}

.cont1Text{
    margin-left: 10px;
    color: white;
}

.block3{
    width: 20%;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: start;
}


.block4{
    width: 30%;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: start;
}

.cont4{
    margin-top: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.cont4Icon{
    height: 30px;
    width: 30px;
    background-color: white;
    border-radius: 30px;
}

.cont4textContainer{
    margin-left: 10px;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: center;
}
