.faqContent {
    margin-top: 40px;
    width: 100%;
    display: flex;
    background-color: white;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
  }
  
  .faqdetailContainer {
    margin-top: 30px;
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: start;
    justify-content: start;
    margin-left: 20px;
  }
  
  .faqImages {
    width: 50%;
    height: 400px;
    background-color: var(--themeColor);
  }
  
  .faqtextHeading1 {
    margin-left: 20px;

    display: flex;
    justify-content: start;
    align-items: start;
    color: var(--themeColorText);
    font-size: 30px;
    font-weight: bold;
  }
  
  .qaContainer {
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: start;
    margin: 16px;
    padding: 10px;
    background-color: var(--bgColor);
    border: 1px solid var(--cardBorderColor);
  }
  
  .questionsAnsContainer {
    display: flex;
    justify-content: start;
    align-items: center;
  }
  .imgarrowContainer {
    margin-left: 20px;
    height: 30px;
    width: 30px;
    background-color: var(--themeColor);
  }
  
  .ansText {
    margin-top: 10px;
    color: var(--lightText);
  }