
.centerImageLeftRightGrid {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.title {
  margin-top: 40px;
  color: blue;
  font-size: 18px;
  font-weight: bold;
  text-transform: uppercase;
}

.subTitle {
  margin-top: 20px;
  color: var(--darkText);
  font-size: 40px;
  font-weight: bold;
}

.serviceSection {
    margin-top: 20px;
    width: 100%;
    display: flex;
  }
  
  .leftSideContent {
    width: 33.3%;
  }
  
  .a1box {
    margin: 16px;
    padding: 10px;
    border: 1px solid var(--cardBorderColor);
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .serviceImage {
    height: 80px;
    width: 80px;
    background-color:white;
  }

  .detailsServiceContainer{
    margin-left: 16px;
  }
  
  .serviceHeading1 {
    font-size: 20px;
    color: var(--darkText);
    font-weight: 500;

  }
  
  .serviceHeading2 {
    margin-top: 4px;
    font-size: 16px;
    color: var(--lightText);
    line-height: 28px;

  }


  .centerImage {
    margin-left: 20px;
    margin-right: 20px;
    width: 33.3%;
  
    background-color: white;
  }
  
  .rightSideContent {
    width: 33.3%;
  }