.topbar {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    display: grid;
    grid-template-columns: auto auto;
  
  }
  
  .rightContainer {
    display: flex;
  }
  
  .firstTopContainer {
    display: flex;
  }
  
  .emailIcon {
    margin-left: 10px;
    height: 20px;
    width: 20px;
    background-color: #5e5d5d;
  }
  
  .textEmail {
    margin-left: 10px;
  }
  
  .secTopContainer {
    margin-left: 20px;
    display: flex;
  }
  
  .mobileIcon {
    margin-left: 10px;
    height: 20px;
    width: 20px;
    background-color: #5e5d5d;
  }
  
  .mobileNumber {
    margin-left: 10px;
  }
  
  .socialiconContainer {
    background-color: #207ef5;
    display: flex;
    margin-right: 10px;
  }
  
  .fbicon {
    margin: 10px;
    background-color: grey;
    height: 26px;
    width: 26px;
  }
  
  .topNavigationBar {
    padding-top: 10px;
    padding-bottom: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-style: none;
  }
  
  .nFirstCotainer {
    display: flex;
    align-items: center;
    border-style: none;
  }

  .navigationMenu {
    display: flex;
    border-style: none;
  }


  .logo {
   border-style: none;	  
}
  
  .Name {
    font-weight: bold;
  }


.headerButtonDiv{
margin-left: 20px;
}

.headerButton{
min-width: 100px;
background-color: white;
font-family: Roboto;
font-size: 18px;
display: flex;
flex-direction: row;
justify-content: center;
border-style: none;
cursor: pointer;
}


.headerButton:hover{
color:var(--themeColor);

}


.headerButtonText{

display: flex;
justify-content: center;
align-items: center;
}


.dropdownOne{
height: 130px;
width: 170px;
border-style: solid;
position: absolute;
padding: 5px;
background-color: white;
}






