


.websiteContent {
  width: 100%;
  display: flex;
  margin-top: 40px;
}

.detailContainer {
  margin-top: 40px;
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: start;
  justify-content: start;
}

.HomeImageContainer {
  width: 50%;
  background-color: var(--themeColor);
}

.textHeading1 {
  font-weight: bold;
  color: var(--themeColor);
  font-size: 25px;
  text-transform: capitalize;
}

.textHeading2 {
  margin-top: 20px;
  font-weight: bold;
  color: var(--darkText);
  line-height: 55px;
  font-size: 45px;
}

.textDetails {
  margin-top: 40px;
  color: var(--lightText);
  font-size: 16px;
  line-height: 28px;
}
