.ourTeam {
  width: 100%;
  margin-top: 40px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.ourTeamTitle {
  font-size: 20px;
  color: var(--themeColor);
  font-weight: bold;
}

.ourTeamSubTitle {
  margin-top: 20px;
  font-size: 30px;
  font-weight: bold;
  color: var(--darkText);
  font-weight: bold;
}

.allTeamMemberdetail {
  margin-top: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.ourTeamdetailContainer {
  width: 250px;
  margin: 10px;
  padding: 10px;
  border: 1px solid var(--cardBorderColor);
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
}

.developerPic {
  border-radius: 10px;

  width: 100%;
  background-color: #207ef5;
  height: 250px;
}

.developerName {
  margin-top: 16px;
  font-size: 18px;
  font-weight: bold;
  color: var(--darkText);
}

.developerRole {
  margin-top: 10px;
  color: var(--themeColor);
}

.socialNetworking {
  margin-top: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
}

.pic1 {
  height: 30px;
  width: 30px;
  background-color: var(--cardBorderColorLight);
  border-radius: 50%;
  border: 1px solid var(--cardBorderColor);
}

.pic2 {
  margin-left: 16px;
  margin-right: 16px;
  height: 30px;
  width: 30px;
  background-color: var(--cardBorderColorLight);
  border-radius: 50%;
  border: 1px solid var(--cardBorderColor);
  color: #0a66c2;
}

.pic3 {
  height: 30px;
  width: 30px;
  background-color: var(--cardBorderColorLight);
  border-radius: 50%;
  border: 1px solid var(--cardBorderColor);
  color: var(--themeColor);
}
