.subscriptionDetails {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.planContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.basicPlan {
  margin: 10px;
  padding: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 33.33%;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px,
    rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
  border-radius: 8px;
}

.basicPlanContent {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.pic {
  margin-top: 20px;
  height: 60px;
  width: 60px;
  background-color: #207ef5;
  border-radius: 20px;
}

.planTitle {
  margin-top: 20px;
  font-size: 25px;
  color: var(--themeColor);
  font-weight: bold;
}

.aboutPrice {
  margin-top: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.amount {
  font-size: 40px;
}

.amountMonth {
  font-size: 20px;
  display: flex;
  align-items: end;
}

.horizontalLine {
  margin-top: 10px;
  margin-bottom: 10px;
  width: 100%;
  background-color: var(--darkText);
  border-bottom: 1px solid var(--cardBorderColor);
}

.features {
  margin-top: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  align-items: center;
}

.checkIcon {
  height: 30px;
  width: 30px;
  border-radius: 50%;
  background-color: gray;
}

.textplanDetails {
  margin-left: 10px;
  color: var(--lightText);
}

.basicPlanBtn {
  height: 24px;
  width: fit-content;
  background-color: var(--themeColor);
  padding: 10px;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 40px;
  border-radius: 10px;
  margin-bottom: 40px;
}

.standardPlan {
  width: 33.33%;
  margin: 10px;
  padding: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 33.33%;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px,
    rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
  border-radius: 8px;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px,
    rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
}

.unlimitedPlan {
  margin: 10px;
  padding: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 33.33%;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px,
    rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
  border-radius: 8px;
  width: 33.33%;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px,
    rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
}

