.footer{
    margin-top: 40px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #207ef5;
    padding: 10px;
}

.block1{
    width: 30%;

    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: start;
}

.logotextContainer{
    display: flex;
    justify-content: center;
    align-items: center;
}

.logopic{
    width: 100px;
    background-color: white;
    height: 50px;
}

.websiteNametext{
    margin-left: 10px;
    font-size: 20px;
}

.websiteDetail{
    margin-top: 10px;
    color: white;
    font-size: 14px;
}


.block2{
    width: 20%;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: start;
}

.cont1{
    margin-top: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.cont1Icon{
    height: 30px;
    width: 30px;
    background-color: white;
    border-radius: 50%;
}

.cont1Text{
    margin-left: 10px;
    color: white;
}

.block3{
    width: 20%;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: start;
}


.block4{
    width: 30%;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: start;
}

.cont4{
    margin-top: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.cont4Icon{
    height: 30px;
    width: 30px;
    background-color: white;
    border-radius: 30px;
}

.cont4textContainer{
    margin-left: 10px;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: center;
}