.bookAppointment {
  margin-top: 40px;
  display: flex;
  width: 100%;
  background-color: var(--cardBorderColorLight);
  box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
}

.bookpic {
  width: 50%;
  background-color: green;
  

  height: 350px;
}

.bookDetail {
  width: 50%;
  height: 350px;
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: start;
}

.bookDetailContainer {
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: start;
  align-content: center;
}

.bookTitle {
  font-size: 20px;
  font-weight: bold;
  color: var(--themeColorText);
}

.booksubTitle {
  margin-top: 30px;
  width: 70%;
  font-size: 40px;
  font-weight: bold;
  color: var(--darkText);
}

.bookaptBtn {
  margin-top: 30px;
  padding: 10px;
  background-color: var(--themeColor);
  border-radius: 10px;
  color: white;
}
