
.latest {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 40px;
    
  }
  
  .latestTitle {
    font-weight: bold;
    font-size: 20px;
    color: var(--themeColor);
  }
  
  .latestsubTitle {
    margin-top: 20px;
    font-weight: bold;
    font-size: 40px;
    color: var(--themeColor);
  }
  
  .newsContainer {
    margin-top: 40px;
    display: flex;
    justify-content: start;
    align-items: start;
  }
  
  .singleContainer {
    margin: 10px;
  
    width: 300px;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: start;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px, rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
  }
  
  .newsImage {
    width: 100%;
    height: 250px;
    background-color: black;
  }
  
  .newsTitle{
      margin-top: 20px;
      margin-left: 10px;
      font-size: 24px;
      font-weight: bold;
      color: var(--darkText);
  }
  
  .newsDesc{
      margin-top: 10px;
      margin-left: 10px;
      font-size: 16px;
      color: var(--lightText);
  }
  
  
  .readMoreBtn{
      margin-top: 20px;
      margin-left: 10px;
      font-size: 16px;
      color: var(--lightText);
      margin-bottom: 20px;
      background-color: var(--themeColor);
      color: white;
      padding: 6px;
      border-radius: 10px;
  }